//      emoji
window.addEventListener('resize', actionResizeWindow);

//     
function actionResizeWindow() {
    const widthWindow = window.innerWidth;

    if (widthWindow < 768) {
        UI.chat.removeEventListener('click', clickBtnEmoji);
        UI.chat.addEventListener('click', clickBtnEmoji);
    } else if (widthWindow >= 768) {
        UI.chat.removeEventListener('click', clickBtnEmoji);
        UI.emojiContainer.classList.remove(CLASS.showEmoji);
    }
}

//    emoji
function clickBtnEmoji(event) {
    const elemTarget = event.target;

    if (elemTarget === UI.btnEmoji) {
        UI.emojiContainer.classList.toggle(CLASS.showEmoji);
    } else if (elemTarget !== UI.btnEmoji) {
        UI.emojiContainer.classList.remove(CLASS.showEmoji);
    }
}
